.secondary {
  color: #5A2CA8;
}

.welcome {
  height: 80vh;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 5rem 3rem;
  margin: 50px 0;
}

.welcome .left {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}

.welcome .left img {
  width: 100%;
}

.welcome .right {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}

.welcome .right h2 {
  font-weight: 900;
  margin-bottom: 2rem;
}

@media screen and (max-width: 768px) {
  .welcome h2 {
    font-size: 1.9rem;
    margin-bottom: 1rem !important;
  }
  .welcome p {
    font-size: .9rem !important;
  }
}

@media screen and (max-width: 425px) {
  .welcome {
    margin-top: 2rem;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
  }
  .welcome * {
    text-align: center;
  }
  .welcome h2 {
    font-size: 1.9rem;
    margin-bottom: 1rem !important;
  }
  .welcome p {
    font-size: .9rem !important;
  }
}
