.secondary {
    color: #5A2CA8;
}
.welcome {
    height: 80vh;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 5rem 3rem;
    margin: 50px 0;
    .left {
        flex: 1;

        img {
            width: 100%;
        }
    }
    .right {
        flex: 1;

        h2 {
            font-weight: 900;
            margin-bottom: 2rem;
        }
    }
}
@media screen and (max-width: 768px) {
    .welcome {
        h2 {
            font-size: 1.9rem;
            margin-bottom: 1rem !important;
        }
        p {
            font-size: .9rem !important;
        }
    }
}

@media screen and (max-width: 425px) {
    .welcome {
        margin-top: 2rem;
        flex-direction: column;
        
        * {
            text-align: center;
        }
        h2 {
            font-size: 1.9rem;
            margin-bottom: 1rem !important;
        }
        p {
            font-size: .9rem !important;
        }
    }
}